import axios from 'axios'
export default {
    data(){
        return{
            name:'Jilubiao',
            iconlist:[],
            en:'',
            loading2:true

        }
    },
    methods:{
        //返回到钢丝绳界面
        fanhui(){
            // console.log("返回到钢丝绳界面")
            var that = this
               window.opener=null;
                window.open('','_self');
                window.close();
        },
        // 下载开始
        xiazai(){
            var that = this
            // console.log("下载Excel------------------->")
            that.axios
                    .get("/export/testmanRecordExcel?entrustNum="+sessionStorage.ewth,{

                    })
                    .then((res=>{
                        // console.log(res)
                        // console.log(res.config.url)
                        var a = res.config.url
                        // window.location.href= a
                        window.open(a)
                    }))
                    .catch((error=>{
                        // console.log(error)
                    }))

        },


        //页面加载完毕是执行
        getbiao(){
            // console.log("我是刚进来的时候加载的东西----------------------->")
         
            var that = this
            that.axios
                    .get("/export/testmanRecordJson?entrustNum="+sessionStorage.ewth,{

                    })
                    .then((res)=>{
                        // console.log(res)
                        // ***********如果没有信息提示没有信息************
                        if(res.data.data == "" || res.data.data == null){
                            for(var a= 0; a < 8; a++){
                                var obj = {
                                    id:a
                                }
                               that.iconlist.push(obj)
                            }
                        }else{
                            this.iconlist = res.data.data

                        }
                this.loading2 = false
                    })
                    .catch((error)=>{
                        // console.log(error)
                    })
        },

        // 当跳转奥这个页面的时候执行这个函数得到这个方法
            shuju(){
                var that = this
                // console.log("用来得到跳转页面传过来的参数------------------>")
                // console.log(that.$route.params.entrustment)
                let ens = that.$route.params.entrustment
                that.en = ens;
                

              
                   //  that.ssd = this.$route.params.entrustment
        //  console.log(this.$route.params.entrustment)
        //  console.log(that.ssd)
            }



    },
    mounted(){
        var that = this
        this.getbiao() 

   
    },
    created(){

        this.shuju()
    }
    
    
    
}
