<template>
  <div
    class="One"
    v-loading="loading2"
    element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <!-- 页面表头提示开始 -->
    <div class="title">
      <div class="title_title">用户列表</div>
      <div class="title_tisi">
        <div class="shuaxin" @click="getdeluser">
          <i class="el-icon-refresh"></i>
          刷新列表
        </div>
        <div class="shuaxin" @click="centerDialogVisible = true">
          <i class="el-icon-refresh"></i>
          添加用户
        </div>
      </div>
    </div>

    <!-- 列表显示内容开始 -->
    <div class="userlist">
      <el-table
        class="deluser_el_table"
        :data="tableData3"
        @row-click="nihai"
        border
        style="width: 100%"
      >
        <el-table-column prop="username" header-align="center" fixed label="用户名"></el-table-column>

        <el-table-column prop="userRank" header-align="center" label="权限"></el-table-column>

        <el-table-column prop="userClass" header-align="center" label="班级"></el-table-column>

        <el-table-column prop="name" header-align="center" label="修改">
          <template slot-scope="scope">
            <el-button
              class="de_el_button"
              data-0="asdf"
              type="text"
              @click="centerDialogVisible2 = true"
            >修改</el-button>
          </template>
        </el-table-column>

        <el-table-column prop="address" header-align="center" label="重置密码">
          <template slot-scope="scope">
            <el-button
              class="de_el_button three"
              type="text"
              @click="centerDialogVisible1 = true"
            >重置</el-button>
          </template>
        </el-table-column>

        <el-table-column prop="address" header-align="center" label="删除用户">
          <template slot-scope="scope">
            <el-button
              class="de_el_button three"
              type="text"
              @click="centerDialogVisible11 = true"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 返回用户列表div结束 -->
    <!-- --------------------------修改用户开始------------------------ -->
    <el-dialog
      title="修改用户"
      :visible.sync="centerDialogVisible2"
      width="600px"
      class="tanchukuang"
      center
    >
      <!-- 修改用户的界面显示开始 -->
      <!-- 第一层开始 -->
      <div class="uesename de">
        <p class="username_p">用户名</p>
        <div class="xg_Div">
          <input type="button" v-model="xgusername" class="xg_input">
        </div>
      </div>

      <!-- 第二层 -->
      <!-- 第三层 -->
      <div class="uesename">
        <div class="deluser_left">用户班级：</div>

        <div class="deluser_right">
          <el-input placeholder="请输入班级" v-model="xgclass" clearable></el-input>
        </div>
      </div>

      <!-- 第四层 -->
      <div class="uesename de">
        <div class="pingfei">
          <el-radio v-model="radio" label="管理员">管理员</el-radio>
        </div>
        <div class="pingfei">
          <el-radio v-model="radio" label="普通用户">普通用户</el-radio>
        </div>
      </div>

      <!-- 修改用户的界面显示结束 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible2 = false" class="quxiao">取 消</el-button>
        <el-button type="primary" @click="centerDialogVisible2 = xgyh()">确 定</el-button>
      </span>
    </el-dialog>
    <!-- ----------------------------------修改结束--------------------------- -->
    <!-- --------------------------重置开始------------------------ -->
    <el-dialog
      title="重置密码"
      :visible.sync="centerDialogVisible1 "
      width="600px"
      class="tanchukuang"
      center
    >
      <!-- 重置密码的界面显示开始 -->
      <!-- 第一层开始 -->
      <div class="uesename de">
        <p class="username_p">用户名</p>
        <div class="xg_Div">
          <input type="button" v-model="czusername" class="xg_input">
        </div>
      </div>

      <!-- 第二层 -->
      <div class="uesename">
        <div class="deluser_left">重置密码：</div>

        <div class="deluser_right">
          <el-input placeholder="请输入密码" type="password" v-model="czpassword" clearable></el-input>
        </div>
      </div>

      <!-- 重置密码的界面显示结束 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible1 = x_Gquxiao()" class="quxiao">取 消</el-button>
        <el-button type="primary" @click="centerDialogVisible1 = czmm()">确 定</el-button>
      </span>
    </el-dialog>

    <!-- ----------------------------重置密码结束------------------------------- -->
    <!-- -----------------------------删除用户开始------------------------------ -->
    <el-dialog
      title="删除用户"
      :visible.sync="centerDialogVisible11 "
      width="600px"
      class="tanchukuang"
      :close-on-click-modal="false"
      center
    >
      <!-- 重置密码的界面显示开始 -->
      <p class="sahnchu_p">删除数据可能会造成数据丢失！</p>

      <!-- 重置密码的界面显示结束 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible11 = false" class="quxiao">取 消</el-button>
        <el-button type="primary" @click="centerDialogVisible11 = shanchu()">确 定</el-button>
      </span>
    </el-dialog>

    <!---------------------------------- 删除用户结束-------------------------- -->
    <!----------------------------------- 添加用户开始-------------------------- -->
    <el-dialog
      title="用户添加"
      :visible.sync="centerDialogVisible"
      width="600px"
      class="tanchukuang"
      center
    >
      <div class="uesename">
        <div class="deluser_left">用户名：</div>

        <div class="deluser_right">
          <el-input placeholder="请输入用户名" v-model="username" clearable></el-input>
        </div>
      </div>

      <div class="uesename">
        <div class="deluser_left">班级：</div>

        <div class="deluser_right">
          <el-input placeholder="请输入班级" v-model="userClass" clearable></el-input>
        </div>
      </div>

      <div class="uesename">
        <div class="deluser_left">用户密码：</div>

        <div class="deluser_right">
          <el-input placeholder="请输入用户密码" type="password" v-model="password" clearable></el-input>
        </div>
      </div>
      <!-- 4用户级别 -->
      <div class="zengjia">
        <div class="pingfei">
          <el-radio v-model="radio" label="管理员">管理员</el-radio>
        </div>
        <div class="pingfei">
          <el-radio v-model="radio" label="普通用户">普通用户</el-radio>
        </div>
      </div>

      <!-- 添加用户结束 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = tJ_quxiao()" class="quxiao">取 消</el-button>
        <el-button type="primary" @click="centerDialogVisible = queding()">确 定</el-button>
      </span>
    </el-dialog>

    <!-- -------------------------- 添加用户结束-------------------------------- -->
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      name: "One",
      centerDialogVisible: false,
      centerDialogVisible1: false,
      centerDialogVisible11: false,
      centerDialogVisible2: false,

      radio: "管理员",
      username: "",
      password: "",
      visible2: false,
      czpassword: "",
      czusername: "",
      xgusername: "",
      xgpassword: "",
      xgclass: "",
      userClass: "",
      iconlist: [],
      usernameId: "",
      deleterId: "",
      tableData3: [],
      input10: "",
      user_ID: "",
      loading2: true
    };
  },
  methods: {
    // 点击之前获取id根用户名
    nihai(row) {
      this.xgusername = row.username;
      this.user_ID = row.id;
      this.czusername = row.username;
      this.xgclass = row.userClass;
    },
    quxiao() {},

    queding() {
      var that = this;

      that.axios
        .post("/user/saveUser", {
          username: that.username,
          password: that.password,
          userClass: that.userClass,
          userRank: that.radio
        })
        .then(res => {
          that.username = "";
          that.userClass = "";
          that.password = "";
          if (res.data.status == 200) {
            that.$message({
              message: "添加用户成功",
              type: "success",
              duration: "1000",
              center: true
            });
            that.getdeluser();
          } else {
            that.$message({
              message: res.data.message,
              duration: "2000",
              type: "warning",
              center: true
            });
          }
        })

        .catch(error => {
          that.$message.error("网络链接失败，请检查网络是否正常");
        });
    },

    //删除用户开始
    shanchu() {
      var that = this;

      that.axios
        .get("user/deleteUser?userId=" + that.user_ID, {})
        .then(res => {
          if (res.data.status == 200) {
            that.$message({
              message: "删除成功",
              type: "success",
              duration: "1000",
              center: true
            });
            that.getdeluser();
          } else {
            that.$message({
              message: res.data.message,
              duration: "2000",
              type: "warning",
              center: true
            });
          }
        })
        .catch(error => {
          that.$message.error("网络链接失败，请检查网络是否正常");
        });
    },
    //删除id
    shanchuid(e) {
      this.deleterId = e;
    },

    //重置密码开始
    czmm() {
      var that = this;

      that.axios
        .post("/user/resetUserPassword", {
          username: that.czusername,
          password: that.czpassword
        })
        .then(res => {
          if (res.data.status == 200) {
            that.$message({
              message: "修改成功",
              type: "success",
              duration: "1000",
              center: true
            });
            that.czpassword = null;
            that.getdeluser();
          } else {
            that.$message({
              message: res.data.message,
              duration: "2000",
              type: "warning",
              center: true
            });
          }
        })
        .catch(error => {
          that.$message.error("网络链接失败，请检查网络是否正常");
          that.czpassword = null;
        });
    },
    //修改用户取消开始
    x_Gquxiao() {
      var that = this;
      that.czpassword = "";
    },

    // 修改用户开始
    xgyh() {
      var that = this;

      that.axios
        .post("/user/updateUser", {
          username: that.xgusername,
          userClass: that.xgclass,
          userRank: that.radio,
          id: that.user_ID
        })
        .then(res => {
          if (res.data.status == 200) {
            that.$message({
              message: "修改成功",
              type: "success",
              duration: "1000",
              center: true
            });
            that.getdeluser();
          } else {
            that.$message({
              message: res.data.message,
              duration: "2000",
              type: "warning",
              center: true
            });
          }
        })
        .catch(res => {
          that.$message.error("网络链接失败，请检查网络是否正常");
        });
    },

    //添加下的取消
    tJ_quxiao() {
      var that = this;
      that.username = "";
      that.userClass = "";
      that.password = "";
    },

    // 获取用户列表
    getdeluser() {
      var that = this;
      if (
        localStorage.id == "" ||
        localStorage.username == "" ||
        localStorage.userRank == "" ||
        localStorage.userClass == ""
      ) {
        this.$message({
          message: "此跳转方法有危险暂不支持",
          type: "warning",
          duration: "2000",
          center: true
        });
        this.$router.push("/");
      } else {
        that.axios
          .get("/user/selectUserList", {})
          .then(res => {
            if (res.data.status == 200) {
              that.$message({
                message: "获取用户列表成功",
                type: "success",
                duration: "1000",
                center: true
              });
              that.tableData3 = res.data.data;
            } else {
              that.$message({
                message: res.data.message,
                duration: "2000",
                type: "warning",
                center: true
              });
            }
            this.loading2 = false;
          })
          .catch(error => {
            that.$message.error("网络链接失败，请检查网络是否正常");
          });
      }
    }
  },
  mounted() {
    this.getdeluser();
  }
};
</script>
