<script>
import axios from "axios";
export default {
  directives: {
    focus: {
      inserted(el, binding) {
        el.focus();
      }
    }
  },
  data() {
    return {
      a: "",
      name: "weituodan",
      woshipi: false,
      pch: "",
      wth: "",
      iconList: [],
      xgpch: "",
      xgwtd: "",
      bbb: "",
      tableData3: [],
      centerDialogVisible: false,
      centerDialogVisible1: false,
      centerDialogVisible2: false,
      centerDialogVisible3: false,
      input10: "",
      change_weituodan: "",
      judge_weituodan: "",
      new_Wtd: "", //新建委托单
      dedaopicihao: "",
      //恢复当前页数
      page: 1,
      total: 0,
      loading2: true,
      batchNum: "",
      old: "",
      oldstate: true,
      fengyefuzhu: 1
    };
  },
  methods: {
    b() {
      var that = this;
      //  console.log("回车啦")
      that.queding1();
    },

    centerState() {
      // console.log("1111");
      var that = this;
      that.centerDialogVisible1 = true;
      document.getElementById("d").focus();
    },

    // ********批量修改取消安努***********
    p_lquxiao() {
      var that = this;
      that.batchNum = "";
    },
    p_lqeding() {
      console.log("批量添加开始------------>");
      var that = this;
      if (that.batchNum == null || that.batchNum == "") {
        that.$message({
          message: "请输入要添加的批次号个数",
          type: "warning",
          duration: "2000",
          center: true
        });
      } else {
        that.axios
          .post("/createEntrustment/saveManyBatch", {
            batchNum: that.batchNum
          })
          .then(res => {
            console.log(res);
            that.batchNum = "";
            if (res.data.message == "ok") {
              that.$message({
                message: "批量添加成功",
                type: "success",
                duration: "1000",
                center: true
              });
              that.batchNum == "";
              that.getweituodan();
              that.centerDialogVisible3 = false;
            } else {
              that.$message({
                message: res.data.message,
                type: "warning",
                duration: "2000",
                center: true
              });
              that.batchNum == "";
            }
          })
          .catch(error => {
            // console.log(error)
          });
      }
    },

    // ***********
    //当点击一行时得到这一行的所有数据
    wtts(row) {
      // console.log(row)

      this.xgpch = row.sampleBatch;
      this.xgwtd = row.entrustmentNumber;
      this.change_weituodan = row.entrustmentNumber;
      var that = this;
      that.centerDialogVisible1 = true;
    },

    cilck111(e) {},
    xiugaitishi() {},
    nihao(e) {
      this.bbb = e;
    },

    open7() {
      this.$notify({
        title: "修改委托单信息",
        message: "您取消了修改委托单操作"
      });
    },
    dedaodongxi(row) {
      console.log(row);
    },

    three(e, a, c) {
      var that = this;

      if (that.oldstate == true) {
        that.old = a;
        that.xgpch = e;
        if (a == null) {
          that.xgwtd = that.tableData3[c].entrustmentNumber;
          that.change_weituodan = null;
        } else {
          that.xgwtd = a;
          that.change_weituodan = that.old;
        }
      }
    },
    //修改委托号开始
    queding1(samplebatch, entNumber, index) {
      var that = this;
      that.xgwtd = that.tableData3[index].entrustmentNumber;
      that.xgpch = samplebatch;
      that.axios
        .post("/entrustment/updateEntrustment", {
          sampleBatch: that.xgpch,
          newEntNum: that.xgwtd,
          oldEntNum: that.old
        })
        .then(res => {
          if (res.data.message == "OK") {
            that.$message({
              message: "修改成功",
              type: "success",
              duration: "1000",
              center: true
            });
            that.handleCurrentChange(that.fengyefuzhu);
            that.oldstate = true;
          } else {
            that.$message({
              message: res.data.message,
              type: "warning",
              duration: "2000",
              center: true
            });
            that.oldstate = false;
          }
          that.handleCurrentChange(that.fengyefuzhu);
        })
        .catch(error => {});
    },

    //   删除委托单小按钮的接口
    shanchu() {
      var that = this;

      that.axios
        .post("/entrustment/deleteEntrustment", {
          sampleBatch: that.xgpch,
          entrustmentNumber: that.change_weituodan
        })
        .then(res => {
          if (res.data.status == 200) {
            that.$message({
              message: "删除成功",
              type: "success",
              duration: "1000",
              center: true
            });
            that.handleCurrentChange(that.fengyefuzhu);
            that.centerDialogVisible = false;
          } else {
            that.$message({
              message: res.data.message,
              duration: "2000",
              type: "warning",
              center: true
            });
            that.handleCurrentChange(that.fengyefuzhu);
          }
        })
        .catch(error => {});
    },

    // 生命周期当页面加载完毕后将样式批次与委托号渲染出来
    getweituodan() {
      var that = this;
      that.new_queding;
      that.page = 1;
      //通过判断是不是链接直接跳转过来的
      if (
        localStorage.id == "" ||
        localStorage.username == "" ||
        localStorage.userRank == "" ||
        localStorage.userClass == ""
      ) {
        // console.log("localStorage 为空 阻止跳转");
        this.$message({
          message: "此跳转方法有危险暂不支持",
          type: "warning",
          duration: "2000",
          center: true
        });
        this.$router.push("/");
      } else {
        that.axios
          .post("/createEntrustment/getEntrustmentList", {
            offset: 1,
            limit: 15
          })
          .then(res => {
            // console.log(res);
            if (res.data.status == 200) {
              that.total = res.data.data.total;
              that.tableData3 = res.data.data.datalist;
              that.xgpch = that.value;
              if (res.data.data.datalist.length == "0") {
                that.a = false;
              } else {
                that.a = true;
              }
            } else {
              this.$message({
                message: res.data.message,
                type: "warning",
                duration: "2000",
                center: true
              });
            }
            that.loading2 = false;
          })

          .catch(error => {
            that.$message.error("网络链接失败，请检查网络是否正常");
          });
      }
    },

    // ****************************增加取消按钮*************************************
    new_quxiao() {
      var that = this;
      that.new_Wtd = "";
    },
    // ****************************增加确定按钮*************************************
    new_queding() {
      var that = this;

      var that = this;
      that.axios
        .post("/createEntrustment/createNewBatch", {})
        .then(res => {
          that.dedaopicihao = res.data.data;
          that.xgpch = res.data.data;
          that.new_queding1();
        })
        .catch(error => {});
    },
    // *******************************修改批次号存储到数据库*****************************************
    new_queding1() {
      var that = this;
      that.axios
        .post("/createEntrustment/saveEntrustment", {
          sampleBatch: that.dedaopicihao
        })
        .then(res => {
          that.getweituodan();
        })
        .catch(error => {});
    },

    // *********************************************得到默认的委托单号*********************************************
    dedaoweituodanhao() {
      var that = this;
      that.axios
        .get(
          "/entrustmentNum/showEntrustmentDefaultNum?batchNum=" +
            that.dedaopicihao,
          {}
        )
        .then(res => {
          // ******************将生成的默认委托单号赋予***************************
          that.new_Wtd = res.data.data;
        })
        .catch(error => {});
    },

    // 当页面改变
    handleSizeChange(val) {},
    handleCurrentChange(val) {
      var that = this;

      that.fengyefuzhu = val;
      this.axios
        .post("/createEntrustment/getEntrustmentList", {
          offset: that.fengyefuzhu,
          limit: 15
        })
        .then(res => {
          if (res.data.status == 200) {
            this.tableData3 = res.data.data.datalist;
          } else {
            that.$message({
              message: res.data.message,
              duration: "2000",
              type: "warning",
              center: true
            });
          }
        })
        .catch(error => {
          that.$message.error("网络链接失败，请检查网络是否正常");
        });
    },
    two(e, a) {
      var that = this;
      that.xgpch = e;
      that.change_weituodan = a;
    }
  },
  //定义生命周期当页面加载完毕后将样式批次与委托号渲染出来
  mounted() {
    this.getweituodan();
    var that = this;
  }
};
</script>
