    height: 42px;
    font-size: 15px;
    line-height: 40px;
    text-align: center;
    font-weight: bold;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    float: left;
    background-color: #e9e6e6;
    color: #696767;
    padding: 0px;
  }
  .right {
    float: left;
    width: 225px;
    height: 42px;
    line-height: 42px;
    font-size: 18px;
    font-weight: bold;
  }
}

.xgwtd {
  width: 350px;
  height: 40px;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  font-size: 13px;
  color: #888888;
  text-indent: 10px;
}
.list_header {
  width: 96%;
  height: 43px;
  line-height: 43px;
  background-color: #fff;
  border: 1px solid #e0e0e0;
  margin: auto;
  margin-top: 10px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  box-sizing: border-box;
  .li {
    width: calc(100% / 3);
    height: 43px;
    float: left;
    text-align: center;
    font-size: 13px;
    color: #888888;
    border-right: 1px solid #e0e0e0;
    box-sizing: border-box;
  }
  .two {
    border-right: none;
  }
}
.ul {
  width: 96%;
  height: 28px;
  background-color: #fff;
  margin: auto;
  border: 1px solid #e0e0e0;
  box-sizing: border-box;
  border-top: none;
  // border-right: none;
  line-height: 28px;
  box-sizing: border-box;

  .li {
    width: calc(100% / 3);
    height: 28px;
    line-height: 28px;
    text-align: center;
    border-right: 1px solid #e0e0e0;
    float: left;
    box-sizing: border-box;
    font-size: 13px;
    color: #888888;
    box-sizing: border-box;
    cursor: pointer;
  }
  .three {
    border-right: none;
    color: #c28f6c;
  }
}
.inputaa {
  font-size: 13px;
  color: #888888;
}
</style> 



