<template>
  <div
    class="shiyanjilu"
    v-loading="loading2"
    element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <!-- 标题 -->
    <div class="shiyanjilu-title">查看试验记录</div>
    <div class="shiyanjilu-concent">
      <div class="content_title">
        <div class="weituohao">
          <div class="sy_Div">
            <div class="sy_Div_text">批次号:</div>
            <div class="sy_Div_input">
              <el-input
                type="text"
                v-model="sampleBatch"
                class="sy_input_wth"
                placeholder="请输入批次号"
              />
            </div>
          </div>
        </div>

        <div class="bao">数据类别：</div>
        <div class="shuju">
          <el-select
            v-model="names"
            clearable
            placeholder="请选择"
            class="shuju_down"
            style="{'height':'30px'}"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>

        <div class="jiqi">
          <div class="sy_Div">
            <div class="sy_Div_text">机器编号:</div>
            <div class="sy_Div_input">
              <el-input
                type="text"
                v-model="machineNumbers"
                class="jq_input"
                placeholder="请输入你的机器编号"
              />
            </div>
          </div>
        </div>

        <el-button @click="chaxun2" type="primary" class="chaxun_button">查询</el-button>
      </div>

      <!-- 表格开始 -->
      <div class="laber">
        <el-table :data="tableData3" height="100%" border class="sy_el_table" style="width: 100%">
          <el-table-column prop="sampleNumber" header-align="center" label="试样编号"></el-table-column>

          <el-table-column prop="sampleBatch" header-align="center" label="试样批次"></el-table-column>

          <el-table-column prop="entrustmentNumber" header-align="center" width="150px" label="委托号"></el-table-column>

          <el-table-column prop="experimentalData" header-align="center" label="试验数据"></el-table-column>

          <el-table-column prop="testMembersNumber" header-align="center" label="试验员编号"></el-table-column>

          <el-table-column prop="type" header-align="center" label="数据类别"></el-table-column>

          <el-table-column prop="machineType" header-align="center" label="数据型号"></el-table-column>

          <el-table-column prop="machineNumber" header-align="center" label="机器编号"></el-table-column>

          <el-table-column prop="createDate" header-align="center" label="日期" width="200px"></el-table-column>
        </el-table>
      </div>
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          class="sy_Pagination"
          layout="prev, pager, next"
          :page-size="21"
          :total="total"
          :current-page.sync="page"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      //恢复当前页数
      page: 1,
      //
      total: 0,
      names: "全部",
      sampleBatch: "",
      machineNumbers: "",
      name: "Shiyanjilu",
      centerDialogVisible: false,
      loading2: true,
      iconLists: [
        {
          id: "001",
          name: "全部"
        },
        {
          id: "002",
          name: "拉力"
        },
        {
          id: "003",
          name: "弯曲"
        },
        {
          id: "004",
          name: "扭转"
        },
        {
          id: "005",
          name: "打结"
        }
      ],
      iconList: [
        {
          id: "1",
          entrustmentNumber: "LMJ980109"
        }
      ],
      tableData3: [
        {
          createDate: "",
          entrustmentNumber: "",
          experimentalData: "",
          id: "",
          machineNumber: "",
          machineType: "",
          sampleBatch: "",
          sampleNumber: "",
          testMembersNumber: "",
          type: ""
        }
      ],
      // 下拉
      options: [
        {
          value: "全部",
          label: "全部"
        },
        {
          value: "拉力",
          label: "拉力"
        },
        {
          value: "弯曲",
          label: "弯曲"
        },
        {
          value: "扭转",
          label: "扭转"
        },
        {
          value: "打结",
          label: "打结"
        }
      ],
      value4: ""
    };
  },
  methods: {
    hh(e) {
      //getAttribute()方法返回指定属性名的属性值
      this.names = e.target.getAttribute("value");
    },

    /* 当用户输入信息查询的时候 ，下方列表只显示用户查询出来的数据 */
    chaxun2() {
      var that = this;

      that.page = 1;
      var chaxun_parameter = {};
      chaxun_parameter = {};

      if (that.names == "全部") {
        chaxun_parameter = {
          sampleBatch: that.sampleBatch,
          machineNumber: that.machineNumbers,
          offset: 1,
          limit: 21
        };
      } else {
        chaxun_parameter = {
          sampleBatch: that.sampleBatch,
          machineNumber: that.machineNumbers,
          type: that.names,
          offset: 1,
          limit: 21
        };
      }
      that.axios
        .post("/sample/selectSampleRecordList", chaxun_parameter)
        .then(res => {
          if (res.data.status == 200) {
            that.total = res.data.data.total;
            that.tableData3 = res.data.data.datalist;
          } else {
            that.$message({
              message: res.data.message,
              type: "warning",
              duration: "2000",
              center: true
            });
          }
        })
        .catch(error => {
          that.$message.error("网络链接失败，请检查网络是否正常");
        });
    },

    //当页面初次渲染加载的数据
    chaxun() {
      var that = this;
      //通过判断是不是链接直接跳转过来的
      if (
        localStorage.id == "" ||
        localStorage.username == "" ||
        localStorage.userRank == "" ||
        localStorage.userClass == ""
      ) {
        this.$message({
          message: "此跳转方法有危险暂不支持",
          type: "warning",
          duration: "2000",
          center: true
        });
        this.$router.push("/");
      } else {
        that.axios
          .post("/sample/selectSampleRecordList", {
            offset: 1,
            limit: 21
          })
          .then(res => {
            if (res.data.status == 200) {
              that.total = res.data.data.total;
              that.tableData3 = res.data.data.datalist;
            } else {
              that.$message({
                message: res.data.message,
                type: "warning",
                duration: "2000",
                center: true
              });
            }
            this.loading2 = false;
          })
          .catch(error => {
            that.$message.error("网络链接失败，请检查网络是否正常");
          });
      }
    },

    // 当页面改变
    handleSizeChange(val) {},
    handleCurrentChange(val) {
      var that = this;
      var chaxun_parameter_1 = {};
      chaxun_parameter_1 = {};
      if (that.names == "全部") {
        chaxun_parameter_1 = {
          sampleBatch: that.sampleBatch,
          machineNumber: that.machineNumbers,
          offset: val,
          limit: 21
        };
      } else {
        chaxun_parameter_1 = {
          sampleBatch: that.sampleBatch,
          machineNumber: that.machineNumbers,
          type: that.names,
          offset: val,
          limit: 21
        };
      }
      that.axios
        .post("/sample/selectSampleRecordList", chaxun_parameter_1)
        .then(res => {
          if (res.data.status == 200) {
            that.tableData3 = res.data.data.datalist;
          } else {
            that.$message({
              message: res.data.message,
              duration: "2000",
              type: "warning",
              center: true
            });
          }
        })
        .catch(error => {
          that.$message.error("网络链接失败，请检查网络是否正常");
        });
    }
  },

  mounted() {
    this.chaxun();
  }
};
</script>


<style>
.sy_el_table {
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}
.sy_el_table tr td {
  height: 20px;
  padding: 0px;
  text-align: center;
  font-size: 10px;
}
.choose-weituodan-input input {
  width: 100%;
  height: 30px;
  background-color: #fff;
}
.sy_input_wth input {
  width: 100%;
  height: 26px;
  background-color: #fff;
  text-align: left;
  border: none;
  padding: 0px;
  text-indent: 2px;
  text-transform: uppercase;
}
.jq_input {
  width: 100%;
  height: 100%;
}

.shuju_down input {
  width: 100%;
  height: 20px;
  border: none;
}
.jq_input input {
  width: 100%;
  height: 100%;
  text-align: left;
  border: none;
  padding: 0px;
  text-indent: 2px;
}
.el-select__caret {
  margin-top: 6px;
}
.sy_Pagination {
  height: 38px;
  width: 100%;
  padding: 0px;
  line-height: 38px;
  margin-top: 10px;
}
.sy_Pagination ul {
  height: 38px;
  line-height: 38px;
}
.sy_Pagination ul li {
  height: 38px;
  line-height: 38px;
  width: 38px;
  margin-left: 5px;
  border-radius: 50%;
  font-size: 13px;
  font-weight: bold;
}
.active {
  background-color: #fff;
}
.sy_Pagination button {
  height: 38px;
  width: 38px;
  border-radius: 50%;
  line-height: 38px;
  margin-top: 20000px;
  font-size: 13px;
  font-weight: bold;
}
.sy_Pagination el-icon {
  line-height: 45px;
}
.el-pager li.btn-quicknext,
.el-pager li.btn-quickprev {
  line-height: 45px;
}
.el-pager li.active {
  border: 1px solid #f1be2f;
}
.el-pagination .btn-next {
  width: 38px;
  height: 38px;
  margin-left: 5px;
}
.el-pagination .btn-prev {
  width: 38px;
  height: 38px;
}
</style>


<style lang="less" scoped>
.shiyanjilu {
  background-color: #f2f2f2;
  width: 100%;
  height: 100%;
  min-width: 1160px;
}
.shiyanjilu-title {
  height: 60px;
  font-size: 30px;
  line-height: 60px;
  padding-left: 40px;
  color: #888888;
  font-weight: bold;
}
.shiyanjilu-concent {
  width: 100%;
  height: 85%;
  margin: auto;
  border-radius: 13px;
}
.shiyanjilu-concent-title {
  width: 93%;
  height: 60px;
  // width: 100%;
  // background: #999;
  padding: 20px 30px;
  border-bottom: 1px solid #979797;
  margin: 0 auto;
  background: #fff;
  padding: 10px 10px;
}

.content_title {
  width: 96%;
  height: 38px;
  margin: auto;
  position: relative;
  margin-left: 2%;
  border-radius: 8px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  background-color: #f2f2f2;
  line-height: 35px;
}
// 表头三项
.weituohao {
  width: 250px;
  height: 20px;
  float: left;
  line-height: 20px;
  margin-left: 10px;
}
.shuju {
  width: 150px;
  height: 26px;
  float: left;
  line-height: 26px;
  margin-top: 3px;
  border-right: 1px solid #989898;
  border-top: 1px solid #bbb7b7;
  border-bottom: 1px solid #989898;
  box-sizing: border-box;
  margin-top: 5px;
  background-color: #fff;
}
//机器
.jiqi {
  width: 250px;
  height: 30px;
  float: left;
  line-height: 30px;
  margin-left: 40px;
}

.button {
  width: 80px;
  height: 26px;
  margin-top: 4px;
  float: right;
  line-height: 26px;
  margin-left: 20px;
  color: #c28f6c;
  border: 1px solid #e7d2c4;
  margin-right: 20px;
  border-radius: 6px;
  text-align: center;
  font-size: 14px;
  font-weight: bold;
}

.chaxun_button {
  width: 80px;
  height: 28px;
  margin-top: 5px;
  padding: 0;
  float: right;
  margin-left: 100px;
  font-size: 14px;
  margin-right: 25px;
}

//表格
.laber {
  width: 96%;
  height: calc(100% - 63px);
  background: #f2f2f2;
  margin-top: 10px;
  margin-left: 2%;
}
.cw_img {
  font-size: 18px;
}
.dialog-footer {
  margin-left: 250px;
}

.sy_Div {
  width: 240px;
  height: 26px;
  line-height: 26px;
  margin-top: 5px;
  border-radius: 4px;
  border: 1px solid #989898;
}
.sy_Div_text {
  width: 70px;
  height: 26px;
  line-height: 26px;
  font-size: 13px;
  color: #696767;
  text-align: center;
  background-color: rgb(233, 230, 230);
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  float: left;
}
.sy_Div_input {
  width: 170px;
  height: 26px;
  font-size: 13px;
  color: #888888;
  line-height: 26px;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  float: left;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  text-align: left;
}
.block {
  height: 38px;
  padding: 0px;
  margin-top: 10px;
  position: relative;
  margin: auto;
  line-height: 38px;
  text-align: center;
}
.bao {
  float: left;
  width: 100px;
  height: 26px;
  background-color: red;
  line-height: 26px;
  color: #696767;
