  font-size: 13px;
  background-color: #e9e6e6;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  margin-top: 5px;
  margin-left: 30px;
  border-left: 1px solid #989898;
  border-top: 1px solid #989898;
  border-bottom: 1px solid #989898;
  box-sizing: border-box;
}
</style>



