// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui';
// import 'element-ui/lib/theme-chalk/index.css';
import './assets/element-variables.scss';
import BScroll from 'better-scroll';
import './assets/iconfont/iconfont'
import betterscroll from 'better-scroll'
import './assets/iconfont/iconfont.css'
import Vuex from 'vuex'
Vue.config.productionTip = false
import Print from 'vue-print-nb'
Vue.use(ElementUI);
Vue.use(Print);  //注册
Vue.use(Vuex)
import axios from 'axios'

Vue.prototype.axios = axios;
// axios.defaults.baseURL = "http://10.2.98.231:8099"
// axios.defaults.baseURL = "http://10.2.98.211:8080"
// axios.defaults.baseURL="http://192.168.146.1:8080"
// axios.defaults.baseURL = "http://10.2.98.1:8080"
// axios.defaults.baseURL = "http://10.2.98.233:8080"
// axios.defaults.baseURL = "http://10.2.98.230:8080"
// axios.defaults.baseURL = "http://127.0.0.1:8080"
// axios.defaults.baseURL = "http://172.20.10.13:8080"
axios.defaults.baseURL = "/"
// axios.defaults.baseURL = "/jl"
/* eslint-disable no-new */

window.onbeforeunload = function (e) {
  e = e || window.event;
 
  // 兼容IE8和Firefox 4之前的版本
  if (e) {
    e.returnValue = '关闭提示';
  }
 
  // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
  return '关闭提示';
};



// 注册一个全局自定义指令 `v-focus`
Vue.directive('focus', {
  // 当被绑定的元素插入到 DOM 中时……
  inserted: function (el) {
    // 聚焦元素
    el.focus();
    // console.log("获得焦点");
  }
})





new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  render: h => h(App)
})


