<template>
  <div class="index clear-float">
    <div class="menu">
      <el-menu
        class="el-menu-vertical-demo jie"
        :default-active="defaultActive"
        background-color="#303133"
        text-color="#C0C4CC"
        active-text-color="#DCDFE6"
        style="min-height:100%;"
        router
      >
        <div class="header-logo">
          <div class="logo"></div>
        </div>
        <el-menu-item index="homepage">
          <i class="iconfont">&#xe501;</i>
          &nbsp;&nbsp;首页
        </el-menu-item>


        <el-menu-item index="weituodan">
          <i class="iconfont">&#xe501;</i>
          &nbsp;&nbsp;委托单
        </el-menu-item>

        <el-submenu index="2">
          <template slot="title">
            <i class="iconfont">&#xe78e;</i>
            &nbsp;&nbsp;数据处理
          </template> 
          <el-menu-item index="shiyanjilu">查看试验记录</el-menu-item>
          <el-menu-item index="cuowuchuli">错误处理列表</el-menu-item>
        </el-submenu>

        <el-menu-item index="SteelWire">
          <i class="iconfont">&#xe628;</i>&nbsp;&nbsp;&nbsp;钢丝报表
        </el-menu-item>

        <el-menu-item index="menzhong">
          <i class="iconfont">&#xe632;</i>&nbsp;&nbsp;&nbsp;钢丝绳
        </el-menu-item>

        <el-submenu index="5">
          <template slot="title">
            <i class="iconfont">&#xe66f;</i>
            &nbsp;&nbsp;用户管理
          </template>
          <el-menu-item index="deluser" v-if="xianshi">&nbsp;&nbsp;用户列表</el-menu-item>
          <el-menu-item index="picihao" v-if="xianshi">&nbsp;&nbsp;机器管理</el-menu-item>
          <el-menu-item index="baogaotongji" v-if="xianshi">&nbsp;&nbsp;报告统计</el-menu-item>
          <el-menu-item index="alterpassword">&nbsp;&nbsp;修改密码</el-menu-item>
        </el-submenu>

        <el-menu-item index="/" @click="exit">
          <i class="iconfont">&#xe501;</i>
          <span>&nbsp;&nbsp;退出</span>
        </el-menu-item>
      </el-menu>
    </div>

    <div class="body">
      <router-view class="router-view"/>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      xianshi: true,
      offsetHeight: 1,
      height: {
        height: ""
      }
    };
  },
  computed: {
    defaultActive: function() {
      return this.$route.path.replace("/", "");
    }
  },
  methods: {
    gethome() {
      // console.log("-----------------------------------");
      // console.log(localStorage.userRank);
      if (localStorage.userRank == "管理员") {
        this.xianshi = true;
        // console.log("你好我是管梨园");
      } else {
        this.xianshi = false;
        // console.log("你好我是普通用户");
      }
    },

    // 退出小按钮
    exit() {
      // console.log("你好啊我是退出的小按钮，清空localStrorage");
      // console.log(localStorage.id);
      // console.log(localStorage.username);
      // console.log(localStorage.userRank);
      // console.log(localStorage.userClass);
      localStorage.id = "";
      localStorage.username = "";
      localStorage.userRank = "";
      localStorage.userClass = "";
      // console.log("id" + localStorage.id);
      // console.log("username" + localStorage.username);
      // console.log("userRank" + localStorage.userRank);
      // console.log("userClass" + localStorage.userClass);
    }
  },
  mounted() {
    this.gethome();
    this.height.height = document.body.offsetHeight;
    // console.log("12313")
    // console.log(document.body.offsetHeight)
  }
};
</script>
<style>
</style>


<style lang='less' scoped>
.clear-float::after {
  content: "";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

.index {
  position: relative;
  width: 100%;
  height: 100%;
  background: #f2f2f2;
  overflow: hidden;
  .menu {
    height: 100%;
    width: 199px;
    float: left;
    background: #303133;
    overflow-y: scroll;
    overflow-x: hidden;
    background-color: red;
    .header-logo {
      height: 150px;
      width: 100%;
      background: #303133;
      overflow: auto;
      .logo {
        margin: auto;
        margin-top: 60px;
        height: 50px;
        width: 110px;
        background-image: url("../assets/img/J&L.png");
        background-size: 100% 100%;
      }
    }
  }
  .body {
    width: calc(100% - 200px);
    height: 100%;
    background: #f2f2f2;
    float: right;
    .header {
      min-width: 1160px;
      height: 50px;
      margin: auto;
      background: #f2f2f2;
      // box-shadow: 0  3px 20px rgba(0, 0,0, 0.2);
      z-index: 10;
      .breadcrumb {
        line-height: 50px;
        font-size: 18px;
        padding-left: 20px;
        color: #555;
        background-color: #f2f2f2;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
      }
    }

    .router-view {
      // height: 100%;
      position: relative;
      background: #f2f2f2;
      height: 100%;
      overflow: scroll;
    }
  }
}
.iconfont {
  width: 25px;
  height: 25px;
}
::-webkit-scrollbar {
  display: none;
}
</style>



