    // 查询新建委托单接口开始
    chaxunxinjian() {
      var that = this;

      that.steelWireDataList = [];

      if (that.names == "" || that.names == null) {
        that.$message({
          message: "委托单号不能为空",
          type: "warning",
          duration: "2000",
          center: true
        });
      } else {
        this.axios
          .post("/entrustment/selectOrSaveEntData", {
            entrustmentNumber: this.entrustmentNumber
          })
          .then(res => {
            if (res.data.status == 200) {
              that.$message({
                message: "查询报表成功",
                type: "success",
                duration: "1000",
                center: true
              });
              that.reviewer_one = res.data.data.reviewer; //审查人
              that.date = res.data.data.date; //时间
              that.steelNumber = res.data.data.steelNumber; //钢丝根数
              that.testClerkNumber = res.data.data.testClerkNumber; //试验员编号
              that.testMachine = res.data.data.testMachine; //试验机编号
              // *************************判断审核人是不是为空********************************************
              if (that.reviewer == "" || that.reviewer == null) {
                that.reviewer = localStorage.username;
              }
              if (
                res.data.data.steelWireDataList == "" ||
                res.data.data.steelWireDataList == null ||
                res.data.data.steelWireDataList == []
              ) {
              } else {
                that.shaodongid = 0;

                for (
                  var i = 0;
                  i < res.data.data.steelWireDataList.length;
                  i++
                ) {
                  res.data.data.steelWireDataList[i].index = i + 1;
                }

                that.steelWireDataList = res.data.data.steelWireDataList; //列表渲染ß

                if (that.steelNumber == "" || that.steelNumber == null) {
                  that.steelNumber = res.data.data.steelWireDataList.length;
                }
              }
            } else {
              that.$message({
                message: res.data.message,
                duration: "2000",
                type: "warning",
                center: true
              });
            }
            if (
              that.steelWireDataList == null ||
              that.steelWireDataList == [] ||
              that.steelWireDataList == ""
            ) {
            } else {
              for (var i = 0; i < res.data.data.steelWireDataList.length; i++) {
                if (res.data.data.steelWireDataList[i].machineType == "DC-KL") {
                  var a = String(
                    res.data.data.steelWireDataList[i].breakTensile
                  );
                  var jie = that.steelWireDataList[i].nDiamete;
                  var jie1 = (parseInt(jie * 100) / 100).toFixed(2);
                  that.steelWireDataList[i].nDiamete = jie1;
                  var b = [];
                  b = a.split(".");
                  var c = [];
                  for (var a = 0; a < 2; a++) {
                    var d = b[1].slice(a, a + 1);
                    if (d == "" || d == null) {
                      d = 0;
                      c.push(d);
                    } else {
                      c.push(d);
                    }
                  }
                  var str = c[0];
                  var strs = c[1];
                  var string = str + strs;
                  var one = b[0];
                  that.steelWireDataList[i].breakTensile = one + "." + string;
                } else if (
                  res.data.data.steelWireDataList[i].machineType == "DC-L"
                ) {
                  var a = String(
                    res.data.data.steelWireDataList[i].breakTensile
                  );
                  var b = [];
                  b = a.split(".");
                  var c = [];
                  for (var a = 0; a < 3; a++) {
                    var d = b[1].slice(a, a + 1);
                    if (d == "" || d == null) {
                      d = 0;
                      c.push(d);
                    } else {
                      c.push(d);
                    }
                  }
                  var str = c[0];
                  var strs = c[1];
                  var strss = c[2];
                  var string = str + strs + strss;
                  var one = b[0];
                  that.steelWireDataList[i].breakTensile = one + "." + string;
                } else {
                }
              }
            }
          })
          .catch(error => {
            that.$message.error("网络链接失败，请检查网络是否正常");
          });
      }
    },
