<template>
  <div
    class="bao"
    v-loading="loading2"
    element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <div class="alterpassword-title">修改密码</div>

    <div class="alterpassword">
      <div class="content">
        <div class="oldpassword alt">
          <div class="old-right1">
            <span class="name">用户名:</span>
            {{oldusername}}
          </div>
        </div>
        <div class="oldpassword">
          <div class="old-right1">
            <div class="old-right1_left">原始密码:</div>

            <div class="old-right1_right">
              <el-input
                type="password"
                class="pass-input"
                placeholder="请输入原始密码"
                v-model="oldpassword"
                clearable
              ></el-input>
            </div>
          </div>
        </div>
        <div class="oldpassword">
          <div class="old-right1">
            <div class="old-right1_left">新密码:</div>

            <div class="old-right1_right">
              <el-input
                type="password"
                class="pass-input"
                placeholder="请输入你的新密码"
                v-model="newpassword"
                clearable
              ></el-input>
            </div>
          </div>
        </div>

        <!-- 确定按钮 -->
        <el-button type="primary" class="get-btn" @click="xiugai">确定修改</el-button>
      </div>
      <div class="pss-logo">
        <div class="pss-logo-img"></div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      name: "Alterpassword",
      oldusername: "",
      oldpassword: "",
      newpassword: "",
      loading2: true
    };
  },
  methods: {
    xiugai() {
      var that = this;

      // 通过判断输入框是不是为空，要是为空的话不进行接口调用直接输出信息不可为空过
      if (that.oldpassword == "" || that.newpassword == "") {
        if (that.oldpassword == "") {
          that.$message({
            message: "原始密码不能为空",
            duration: "2000",
            type: "warning",
            center: true
          });
        } else {
          that.$message({
            message: "新密码不能为空",
            duration: "2000",
            type: "warning",
            center: true
          });
        }
      } else {
        that.axios
          .post("/user/updateUserPassword", {
            username: that.oldusername,
            oldPassword: that.oldpassword,
            newPassword: that.newpassword
          })
          .then(res => {
            if (res.data.status == 200) {
              that.$message({
                message: "修改密码成功",
                type: "success",
                duration: "1000",
                center: true
              });
              that.$router.push("/");
            } else {
              that.$message({
                message: res.data.message,
                duration: "2000",
                type: "warning",
                center: true
              });
            }
          })
          .catch(error => {
            that.$message.error("网络链接失败，请检查网络是否正常");
          });
      }
    },
    tishi() {
      this.oldusername = localStorage.username;
      if (
        localStorage.id == "" ||
        localStorage.username == "" ||
        localStorage.userRank == "" ||
        localStorage.userClass == ""
      ) {
        this.$message({
          message: "此跳转方法有危险暂不支持",
          type: "warning",
          duration: "2000",
          center: true
        });
        this.$router.push("/");
      }
    }
  },
  mounted() {
    this.tishi();
    this.loading2 = false;
  }
};
</script>

<style>
.pass-input input {
  height: 50px;
  line-height: 50px;
  border-radius: 8px;
  border: none;
  background: #fafafa;
}
</style>

<style lang='less' scoped>
.bao {
  width: 100%;
  height: 100%;
  min-width: 1030px;
  position: relative;
}
.alterpassword-title {
  height: 60px;
  font-size: 30px;
  line-height: 60px;
  padding-left: 40px;
  color: #888888;
  font-weight: bold;
}

.alterpassword {
  width: 100%;
  min-height: 650px;
  margin-left: 80px;
  border-radius: 8px;
  position: relative;
  overflow: hidden;
}
.content {
  width: 450px;
  min-height: 600px;
  padding-left: 50px;
  border-radius: 8px;
  background-color: #fafafa;
  box-shadow: 0 5px 40px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  float: left;
}
.pss-logo {
  // background: #789;
  height: 600px;
  width: calc(100% - 500px);
  float: left;
  .pss-logo-img {
    // margin: auto;
    margin-left: 300px;
    margin-top: 170px;
    height: 200px;
    width: 160px;
    background-image: url("../assets/img/lock_logo.png");
    background-size: 100% 100%;
  }
}
.oldpassword {
  margin-top: 50px;
  width: 80%;
  height: 60px;
  border-radius: 8px;
  border: 1px solid #989898;
  box-sizing: border-box;
}

.get-btn {
  width: 130px;
  height: 45px;
  border-radius: 8px;
  font-size: 16px;
  color: #f2f2f2;
  margin-top: 70px;
}

.old-right {
  width: 38%;
  float: left;
  position: absolute;
  border: 1px solid #ccc;
  line-height: 38px;
  margin-left: 180px;
}
.old-right1 {
  // border: 1px solid #999;
  border-radius: 8px;
  width: 350px;
  color: #888;
  font-size: 30px;
  box-sizing: border-box;
}
.name {
  font-size: 25px;
  color: #999;
  margin-right: 20px;
}

.input-right {
  width: 98%;
  height: 100%;
  font-size: 13px;
  font-weight: bold;
  overflow: hidden;
  text-indent: 8px;
  background-color: #fff;
}
.old-right1_left {
  width: 100px;
  height: 58px;
  font-size: 16px;
  line-height: 60px;
  text-align: center;
  font-weight: bold;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  float: left;
  box-sizing: border-box;
  float: left;
  background-color: rgb(233, 230, 230);
  color: #696767;
}
.old-right1_right {
  float: left;
  width: 250px;
  height: 58px;
  line-height: 58px;
  font-size: 18px;
  font-weight: bold;
}
.alt {
  border: none;
}
</style>



