      that.ids.push(goId);
      // 进行判断如果数组中有这个了就把这个删除掉
      for (var i = 0; i < that.ids.length; i++) {
        for (var j = 0; j < that.ids.length - 1; j++) {
          if (that.ids[j].id == that.delete_id) {
            that.ids.splice(j, 2);
          }
        }
      }
      // *******************************这里是动态增加的数组****************************************
      if (that.ids == null || that.ids.length == 0) {
        that.ifshow = false;
      } else {
        that.ifshow = true;
