              for (var i = 0; i < ch.length; i++) {
                ch[i].checked = false;
              }

              that.ids = [];
              that.ifshow = false;
              that.qxlist = res.data.data.datalist;
            } else {
              that.$message({
                message: res.data.message,
                type: "warning",
                duration: "2000",
                center: true
              });
