      console.log(a);
      var d = e;
      console.log(that.pchs);
      that.xiugai_pch_dan = a;
      this.centerDialogVisible11 = true;
    },
    DoCheck() {
      var that = this;
      that.ids = [];
      var ch = document.getElementsByName("wp");
      if (document.getElementsByName("allChecked")[0].checked == true) {
        for (var i = 0; i < ch.length; i++) {
          ch[i].checked = true;
          var obj = {
            id: that.tableData3[i].id
          };
          that.ids.push(obj);
          console.log(that.ids);
        }
      } else {
        for (var i = 0; i < ch.length; i++) {
          ch[i].checked = false;
        }
      }
    },
    likai(e) {
      console.log("光标离开了");
      console.log(e);
      var that = this;
      var a = that.tableData3[e].id;
      var b = that.tableData3[e].sampleNumber;
      var c = parseInt (that.tableData3[e].sampleBatch);
      console.log(a);
      console.log(b);
      console.log(c)
      that.axios
        .post("/sample/updateSampleNumber", {
          id: a,
          sampleNumber: b,
          sampleBatch:c
        })
        .then(res => {
          console.log(res);
          if (res.data.message == "OK") {
            this.$message({
              message: "修改成功",
              type: "success"
            });
          } else {
            that.$message({
              message: res.data.message,
              type: "warning",
              duration: "2000",
              center: true
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    sC(e) {
      console.log(e);
      var that = this;
      that.delete_id = e;
      that.centerDialogVisible20 = true;
    },
    pL_dan(e) {
      console.log(e);
      var that = this;
      that.ids = [];
    }
  },
  mounted() {
    var that = this;
    that.loading2 = false;
    // *******判断是不是管理圆 然后进行按钮的显示********
    that.ifishow();
  }
};
</script>
