<template>
  <div
    class="baogaotongji"
    v-loading="loading2"
    element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <div class="title">报告统计</div>

    <!-- *搜索框** -->
    <div class="sousuo">
      <div class="one">
        <div class="left">产品编号</div>

        <div class="right">
          <el-input placeholder="请输入产品编号" v-model="reportNumbers" class="BG_input" clearable></el-input>
        </div>
      </div>

      <div class="one">
        <div class="left">执行标准</div>

        <div class="right">
          <el-input placeholder="请输入执行标准" v-model="judgeStandards" class="BG_input" clearable></el-input>
        </div>
      </div>

      <el-button type="primary" @click="infoBG" class="chaxun_button">刷新</el-button>

      <el-button type="primary" @click="chaxun" class="chaxun_button">查询</el-button>

      <el-button type="primary" @click="d_C" class="chaxun_button">导出</el-button>
    </div>

    <div class="conter">
      <el-table :data="tableData" style="width: 100%" class="BG" @row-click="rowss">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="备注:">
                <span>{{ props.row.memo}}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>

        <el-table-column label="序号" min-width="48px" type="index"></el-table-column>

        <el-table-column prop="enstrustmentNumber" label="委托号" min-width="145px"></el-table-column>

        <el-table-column prop="structure" label="结构" min-width="130px"></el-table-column>

        <el-table-column prop="specification" label="规格" min-width="60px"></el-table-column>

        <el-table-column prop="producerNumber" label="产品编号" min-width="130px"></el-table-column>

        <el-table-column prop="judgeStandard" label="执行标准" min-width="140px"></el-table-column>

        <el-table-column prop="strengthLevel" label="强度级别" min-width="100px"></el-table-column>

        <el-table-column prop="evaluation" label="判定结果" min-width="80px"></el-table-column>

        <el-table-column prop="memo" min-width="80px" label="备注" class="show"></el-table-column>

        <el-table-column prop="enstrustmentNumber" min-width="240px" label="委托号" v-if="false"></el-table-column>

        <el-table-column min-width="100px" label="操作" header-align="center">
          <template slot-scope="scope">
            <div class="bg_Zh" @click="bg_ZHBG" type="text">综合报告</div>
          </template>
        </el-table-column>

        <el-table-column min-width="120px" label="查看" header-align="center">
          <template slot-scope="scope">
            <div class="bg_Zh" @click="bg_CGSYJU">拆股实验记录</div>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="block">
      <el-pagination
        class="sy_Pagination"
        layout="prev, pager, next"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="page"
      ></el-pagination>
    </div>



   
  </div>
</template>




<script>
import axios from "axios";
export default {
  data() {
    return {
      name: "baogaotongji",
      enstrustmentNumber: "",
      tableData: [
        {
          reportNumber: "", //报告编号
          structure: "", //结构
          specification: "", //规格
          producerNumber: "", //产品编号
          judgeStandard: "", //执行标准
          strengthLevel: "", //强度级别
          evaluation: "", //判断结果
          memo: "" //备注
        }
      ],
      judgeStandards: "", //执行标准
      reportNumbers: "", //报告编号
      total: 0,
      page: 1,
      loading2: true
    };
  },
  methods: {
    formatter(row, column) {
      return row.address;
    },

    // ************点击一行的时候的这一行的所有内容*********
    rowss(e) {
      var that = this;
      console.log(e)
      that.enstrustmentNumber = e.enstrustmentNumber;
      sessionStorage.wth = e.enstrustmentNumber;
    },

    //   **************按照条件查询*******************
    chaxun() {
      var that = this;
      that.page = 1;
      that.axios
        .get(
          "/wireRope/selectWireRopeList?producerNumber=" +
            that.reportNumbers +
            "&judgeStandard=" +
            that.judgeStandards +
            "&offset=" +
            1 +
            "&limit=" +
            10,
          {}
        )
        .then(res => {
          if (res.data.message == "OK") {
            that.$message({
              message: "查询成功",
              type: "success",
              duration: "1000"
            });
            that.tableData = res.data.data.datalist;
          } else {
            that.$message({
              message: res.data.message,
              type: "warning",
              duration: "2000",
              center: true
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    },

    //   ******生命周期加载完毕查询的机器数据*****
    infoBG() {
      var that = this;
      that.axios
        .get(
          "/wireRope/selectWireRopeList?producerNumber=" +
            "" +
            "&judgeStandard=" +
            "" +
            "&offset=" +
            1 +
            "&limit=" +
            10,
          {}
        )
        .then(res => {
          if (res.data.message == "OK") {
            that.$message({
              message: "刷新成功",
              type: "success",
              duration: "1000"
            });
            that.tableData = res.data.data.datalist;
            that.total = res.data.data.total;
            this.loading2 = false;
          } else {
            that.$message({
              message: res.data.message,
              type: "warning",
              duration: "2000",
              center: true
            });
            this.loading2 = false;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },

    // 当页面改变
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },

    handleCurrentChange(val) {
      var that = this;
      console.log(`当前页: ${val}`);
      console.log("当前页数改变了");
      that.axios
        .get(
          "/wireRope/selectWireRopeList?producerNumber=" +
            that.reportNumbers +
            "&judgeStandard=" +
            that.judgeStandards +
            "&offset=" +
            val +
            "&limit=" +
            10,
          {}
        )
        .then(res => {
          if (res.data.message == "OK") {
            that.tableData = res.data.data.datalist;
          } else {
            that.$message({
              message: res.data.message,
              type: "warning",
              duration: "2000",
              center: true
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    },

    // ************管理员查看综合报告******************
    bg_ZHBG() {
      var that = this;
      console.log("点击了按钮")
      console.log(that.enstrustmentNumber)
      if (that.enstrustmentNumber == null || that.enstrustmentNumber == "") {
      } else {
        that.axios
          .get(
            "/export/steelWireReportJson?entrustNum=" + that.enstrustmentNumber,
            {}
          )

          .then(res => {
            if (res.data.status == 200) {
              let routeData = this.$router.resolve({
                name: "one"
              });
              window.open(routeData.href, "_blank");
            } else {
              that.$message({
                message: res.data.message,
                type: "warning",
                duration: "2000",
                center: true
              });
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },

    //拆股实验记录
    bg_CGSYJU() {
      var that = this;
      if (that.enstrustmentNumber == null || that.enstrustmentNumber == "") {
      } else {
        that.axios
          .get(
            "/export/breakStockReportJson?entrustNum=" +
              that.enstrustmentNumber,
            {}
          )
          .then(res => {
            if (res.data.status == 200) {
              let routeData = this.$router.resolve({
                name: "two",
                params: {
                  entrustNum: that.enstrustmentNumber
                }
              });
              window.open(routeData.href, "_blank");
            } else {
              that.$message({
                message: res.data.message,
                type: "warning",
                duration: "2000",
                center: true
              });
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    d_C() {
      var that = this;
      that.axios
        .get(
          "/export/AccountReportExcel?producerNumber=" +
            that.reportNumbers +
            "&judgeStandard=" +
            that.judgeStandards,
          {}
        )
        .then(res => {
          console.log(res)
          if(res.status == "200"){
            that.$message({
              message: "下载成功,请到相应目录查看",
              type: "success",
              duration: "1000"
            });

          }else{
            that.$message({
              message: res.data.message,
              type: "warning",
              duration: "2000",
              center: true
            });
          }
        })
        .catch(error => {});
    }
  },

  mounted() {
    var that = this;
    that.infoBG();
  }
};
</script>

<style>
.el-table .cell {
  overflow: hidden;
  white-space: nowrap !important;
  text-overflow: ellipsis;
}
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
.BG {
  border-radius: 8px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  position: relative;
}
.BG tr td {
  height: 33px;
  padding: 0px;
}
.BG_input input {
  padding: 0px;
  border: none;
  height: 20px;
  text-indent: 3px;
  font-size: 13px;
  color: #888888;
}
/* 分页 */
.sy_Pagination {
  height: 38px;
  width: 100%;
  padding: 0px;
  line-height: 38px;
  margin-top: 10px;
}
.sy_Pagination ul {
  height: 38px;
  line-height: 38px;
}
.sy_Pagination ul li {
  height: 38px;
  line-height: 38px;
  width: 38px;
  margin-right: 5px;
  border-radius: 50%;
  font-size: 13px;
  font-weight: bold;
}
.active {
  background-color: #fff;
}
.sy_Pagination button {
  height: 38px;
  width: 38px;
  border-radius: 50%;
  line-height: 38px;
  margin-top: 20000px;
  font-size: 13px;
  font-weight: bold;
}
.sy_Pagination el-icon {
  line-height: 45px;
}
.el-pager li.btn-quicknext,
.el-pager li.btn-quickprev {
  line-height: 45px;
}
.el-pager li.active {
  border: 1px solid #f1be2f;
}
.el-pagination .btn-next {
  width: 38px;
  height: 38px;
}
.el-pagination .btn-prev {
  width: 38px;
  height: 38px;
  margin-right: 5px;
}
</style>


<style lang="less" scoped>
.baogaotongji {
  width: 100%;
  height: 100%;
  min-width: 1160px;
  background-color: #f2f2f2;
  overflow: hidden;
}

.title {
  width: 100%;
  height: 60px;
  font-size: 30px;
  line-height: 60px;
  color: #888888;
  font-weight: bold;
  text-indent: 40px;
}
.sousuo {
  width: 96%;
  height: 38px;
  margin: auto;
  position: relative;
  margin-left: 2%;
  border-radius: 8px;
  -webkit-box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  background-color: #f2f2f2;
  line-height: 35px;
  .one {
    width: 240px;
    height: 26px;
    line-height: 26px;
    margin-top: 5px;
    border-radius: 4px;
    border: 1px solid #989898;
    margin-left: 10px;
    float: left;
    .left {
      width: 70px;
      height: 26px;
      line-height: 26px;
      font-size: 13px;
      color: #696767;
      text-align: center;
      background-color: #e9e6e6;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
      float: left;
    }
    .right {
      width: 170px;
      height: 26px;
      font-size: 13px;
      color: #888888;
      line-height: 26px;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
      float: left;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
      text-align: left;
    }
  }
  .chaxun_button {
    width: 80px;
    height: 28px;
    margin-top: 5px;
    padding: 0;
    float: right;
    margin-right: 25px;
    font-size: 14px;
  }
}
.conter {
  min-width: 1000px;
  margin: 0 auto;
  max-width: 96%;
  max-height: 75%;
  border-radius: 9px;
  overflow: hidden;
  background-color: #fcfcfc;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  margin-top: 10px;
  overflow: scroll;
  position: relative;
}
.block {
  position: absolute;
  bottom: 25px;
  width: 100%;
  height: 38px;
  padding: 0px;
  margin: auto;
  line-height: 38px;
  text-align: center;
}

.bg_Zh {
  color: #c28f6c;
  font-size: 13px;
  text-align: center;
  cursor: pointer;
  height: 14px;
  width: 100%;
  line-height: 16px;
  z-index: -10;
}
.show {
  width: 100px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 30px;
}
</style>




